<template>
	<view class="content">
		<!-- 背景图 -->
		<image :src="bgImg" class="bgImg"></image>
		<!-- 顶部 -->
		<scroll-view class="scrollBox" scroll-y="true" @scroll="scrollLy">
			<view class="headImgBox">
				<image :src="bgImg" mode="widthFix"></image>
			</view>
			<view class="textBox">
				<view class="score">
					<text class="scoreK">评分：</text>
					<text class="scoreN" v-if="date.rating.score">{{date.rating.score}}</text>
					<text class="scoreN scoreNmini" v-else>暂无评分</text>
				</view>
				<view class="airTime">
					<text class="airTimeK">首播时间：</text>
					<text class="airTimeN">{{date.air_date}}</text>
				</view>
				<view class="abstract">
					<text>简介：{{date.summary==''?'暂无简介':date.summary}}</text>
				</view>
			</view>
		</scroll-view>
		<!-- 返回组件 -->
		<view class="toBack" @click="popupShow()" v-if="isPopup" :class="[isPopup?'animate__animated animate__bounceInLeft delay1':'animate__animated animate__fadeOut']">
			<image src="../../static/img/back.png" mode="widthFix"></image>
		</view>
	</view>
</template>

<script>
	import {mixin} from '../../static/js/mixin.js'
	export default {
		mixins:[mixin], 
		props:['isPopup'],
		data() {
			return {
				date:null,//数据
				bgImg:null,//背景图
			}
		},
		onLoad() {
		},
		methods: {
			// 滚动事件
			scrollLy(e){
				// console.log('111',e.detail.scrollTop)
			},
			//弹出层隐藏
			popupShow(){
				this.$emit('popupShow')
			},
			// 请求数据
			getDate(e,img=null){
				this.bgImg=img
				console.log(img,'11111')
				this.$api.getDetail(e).then(res => {
				   console.log(res,'成功')
				   this.date=res.data
				})
			},
		}
	}
</script>

<style lang="scss" scoped>
@import '../../static/css/popupPage.scss'
</style>